import React from 'react';
import { useEffect, useState } from 'react';
import { Link, Outlet, useNavigate } from 'react-router-dom';
import Lottie from 'lottie-react';
import Loading from '../assets/lottie/loading_lotie.json';

import { searchMovie } from '../api';

export function Navbar() {
  const baseImageUrl = process.env.REACT_APP_IMAGE_URL;
  const navigate = useNavigate();

  const searchOutput = document.getElementById('search_output');
  const searchBar = document.getElementsByName('search')[0];

  const navigateMe = (id, path) => {
    navigate(`/${path}`, { replace: true, state: { id: `${id}` } });
    searchOutput.classList.add('hidden');
    searchBar.value = '';
  };

  const [searchMovies, setSearchMovies] = useState([]);
  const [isHover, setIsHover] = useState(0);
  const [loading, setLoading] = useState(false);

  const handleScroll = () => {
    const position = window.pageYOffset;
    navChange(position);
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScroll, { passive: true });
    navigate('/', { replace: true });
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
    // eslint-disable-next-line
  }, []);

  const navChange = (position) => {
    const navbar = document.getElementById('navbar');
    if (position > 0) {
      navbar.classList.remove('py-6');
      navbar.classList.add('py-4');
      navbar.classList.add('bg-navbarColor');
      navbar.classList.add('backdrop-blur-sm');
    } else {
      navbar.classList.remove('backdrop-blur-sm');
      navbar.classList.remove('bg-navbarColor');
      navbar.classList.remove('py-4');
      navbar.classList.add('py-6');
    }
  };

  const search = async (value) => {
    if (value.length > 3) {
      setLoading(true);
      const searchQuery = await searchMovie(value);
      setSearchMovies(searchQuery.results);
      setLoading(false);
    } else {
      setSearchMovies([]);
    }
  };

  const handleSearch = (value, isOpen) => {
    if (isOpen === 1) {
      value.classList.remove('w-full');
      value.classList.add('w-96');
      searchOutput.classList.remove('hidden');
    } else {
      if (value.value === '' || value.value === null) {
        value.classList.remove('w-96');
        value.classList.add('w-full');
      }
      if (isHover === 0) {
        searchOutput.classList.add('hidden');
      }
    }
  };

  const searchMoviesList = searchMovies.map((movie) => {
    if (movie.poster_path !== null && movie.media_type !== 'person') {
      return (
        <li className="w-full flex gap-5 p-3 h-28 border-b border-b-white/20 hover:cursor-pointer" key={movie.id} onClick={() => navigateMe(movie.id, 'movie')}>
          <img src={movie.poster_path ? `${baseImageUrl}/${movie.poster_path}` : 'undefined'} alt={movie.id} className="rounded-2xl w-[4rem] object-cover opacity-[85%]" />
          <div className="flex flex-col">
            <span className=" text-whitePrimary">{movie.title || movie.name}</span>
            <span className="text-xs text-whitePrimary/80 flex gap-0.5">
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" strokeWidth="1.5" className="w-4 h-4 fill-yellow-600 stroke-yellow-600">
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M11.48 3.499a.562.562 0 011.04 0l2.125 5.111a.563.563 0 00.475.345l5.518.442c.499.04.701.663.321.988l-4.204 3.602a.563.563 0 00-.182.557l1.285 5.385a.562.562 0 01-.84.61l-4.725-2.885a.563.563 0 00-.586 0L6.982 20.54a.562.562 0 01-.84-.61l1.285-5.386a.562.562 0 00-.182-.557l-4.204-3.602a.563.563 0 01.321-.988l5.518-.442a.563.563 0 00.475-.345L11.48 3.5z"
                />
              </svg>
              {movie.vote_average}
            </span>
            <span className="text-xs text-whitePrimary/80">{movie.release_date || movie.first_air_date}</span>
          </div>
        </li>
      );
    }
    return null;
  });

  return (
    <>
      <nav className="w-full flex justify-between items-center px-24 py-6 fixed top-0 z-10 duration-250" id="navbar">
        <div className="flex items-center gap-32">
          <Link to="/" className="text-3xl font-medium group" onClick={() => window.scrollTo(0, 0)}>
            <span className="text-whitePrimary group-hover:text-primary duration-300">Rou</span>
            <span className="text-primary group-hover:text-whitePrimary duration-300">vie</span>
          </Link>
          <div className="flex justify-start items-center font-medium text-xl gap-8">
            <Link to="/" className="duration-250 text-center text-whitePrimary hover:text-primary hover:-translate-y-0.25">
              Genre
            </Link>
          </div>
        </div>
        <div className="flex justify-center items-center gap-16">
          <div className="flex relative" id="test">
            <input
              type="text"
              name="search"
              onChange={({ target }) => search(target.value)}
              className="w-full text-white bg-searchBar px-3 rounded-lg py-2 opacity-90 duration-300 ease-in-out"
              autoComplete="off"
              placeholder="Search..."
              onFocus={({ target }) => handleSearch(target, 1)}
              onBlur={({ target }) => handleSearch(target, 0)}
            />
            {!loading ? (
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="none" className="w-5 h-6 absolute fill-whitePrimary/50 right-2 self-center hover:cursor-pointer hover:fill-whitePrimary duration-250">
                <path fillRule="evenodd" d="M9 3.5a5.5 5.5 0 100 11 5.5 5.5 0 000-11zM2 9a7 7 0 1112.452 4.391l3.328 3.329a.75.75 0 11-1.06 1.06l-3.329-3.328A7 7 0 012 9z" clipRule="evenodd" />
              </svg>
            ) : (
              <Lottie animationData={Loading} className="w-14 absolute right-0 self-center" />
            )}
            <div className="w-full mt-14 absolute bg-primaryDark/95 text-white group hidden" id="search_output">
              <ul className="parent flex flex-col items-center overflow-y-auto -scroll-m-10 scrollbar-hide min-h-0 max-h-[90vh]" id="parent" onMouseOver={({ target }) => setIsHover(1)} onMouseLeave={({ target }) => setIsHover(0)}>
                {searchMoviesList}
              </ul>
            </div>
          </div>
          <button className="flex justify-center items-center text-whitePrimary hover:text-primary duration-250 font-medium text-lg group">
            <span className="w-max">Sign in</span>
            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6 stroke-primary group-hover:translate-x-0.5 group-hover:rotate-2 duration-300">
              <path strokeLinecap="round" strokeLinejoin="round" d="M15.75 9V5.25A2.25 2.25 0 0013.5 3h-6a2.25 2.25 0 00-2.25 2.25v13.5A2.25 2.25 0 007.5 21h6a2.25 2.25 0 002.25-2.25V15m3 0l3-3m0 0l-3-3m3 3H9" />
            </svg>
          </button>
        </div>
      </nav>
      <Outlet />
    </>
  );
}
