import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, EffectFade } from 'swiper';
import { useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';
import Lottie from 'lottie-react';
import Loading from '../assets/lottie/loading_lotie.json';
import { getGenres, getMoviePopular, getMovieLatest, genreConverter, getMovieTrending } from '../api';

import Puss from '../assets/images/Dashboard/Puss_in_boots_Dashboard.jpg';
import SuperM from '../assets/images/Dashboard/supermario.jpg';
import Santa from '../assets/images/Dashboard/Santa.jpg';
import 'swiper/css';
import 'swiper/css/effect-fade';

export const Home = () => {
  const navigate = useNavigate();
  const [mainLoading, setMainLoading] = useState(false);
  const [movies, setMovies] = useState([]);
  const [genres, setGenres] = useState([]);
  const [selectedGenre, setSelectedGenre] = useState('All Genre');
  const [btnFilter, setBtnFilter] = useState(1);
  const [btnGenre, setBtnGenre] = useState(0);
  const baseImageUrl = process.env.REACT_APP_IMAGE_URL;

  const bt1_w = 78;
  const bt2_w = 68;
  const bt3_w = 69;
  const btn_h = 26;

  useEffect(() => {
    const animationBg = document.getElementById('animationBg');
    if (btnFilter === 1) {
      animationBg.style.width = `${bt1_w}px`;
      animationBg.style.left = '1rem';
      getMovieTrending().then((results) => {
        setMovies(results);
      });
      setBtnFilter(1);
    } else if (btnFilter === 2) {
      animationBg.style.width = `${bt2_w}px`;
      animationBg.style.left = '6.45rem';

      getMovieLatest().then((results) => {
        setMovies(results);
      });

      setBtnFilter(2);
    } else if (btnFilter === 3) {
      animationBg.style.width = `${bt3_w}px`;
      animationBg.style.left = '11.219rem';
      getMoviePopular().then((results) => {
        setMovies(results);
      });
      setBtnFilter(3);
    } else {
      animationBg.style.width = `0`;
      animationBg.style.left = '0';
    }
  }, [btnFilter]);

  useEffect(() => {
    getMovieTrending().then((result) => {
      setMainLoading(true);
      setMovies(result);
      setMainLoading(false);
    });

    getGenres().then((result) => {
      setGenres(result);
    });
  }, []);

  const navigateMe = (id, path, type) => {
    navigate(`/${path}`, { replace: true, state: { id: `${id}`, type: `${type}` } });
  };

  const getGenresList = genres.map((genre) => {
    return (
      <li
        value={genre.name}
        key={genre.id}
        className="active:text-primary border-b-[1px] border-b-white/20 py-1 px-1 hover:bg-primary/30"
        onClick={() => {
          getMoviePopular(undefined, undefined, genre.id).then((result) => {
            setMovies(result);
          });
          setSelectedGenre(genre.name);
        }}
      >
        <span>{genre.name}</span>
      </li>
    );
  });
  console.log(movies);

  const popularMoviesList = movies.map((movie) => {
    const finalDate = new Date(movie.release_date || movie.first_air_date).getFullYear();
    if (movie.poster_path !== null && movie.media_type !== 'person') {
      return (
        <div key={movie.id} className="w-52 flex flex-col mb-4 justify-between font-quicksand">
          <span className="w-full hover:cursor-pointer rounded-3xl duration-300 overflow-hidden relative group flex" onClick={() => navigateMe(movie.id, 'movie', movie.media_type)}>
            <img src={`${baseImageUrl}/${movie.poster_path}`} alt="Not Available" className="rounded-3xl group-hover:scale-110 duration-300 group-hover:blur-sm" />
            <span className="w-full h-full group-hover:bg-black/40 absolute self-center duration-300"></span>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" className="w-14 h-14 fill-primary/70 absolute self-center right-1/2 translate-x-1/2 opacity-0 group-hover:opacity-100 group-hover:w-9 group-hover:h-9 duration-500">
              <path strokeLinecap="round" strokeLinejoin="round" d="M5.25 5.653c0-.856.917-1.398 1.667-.986l11.54 6.348a1.125 1.125 0 010 1.971l-11.54 6.347a1.125 1.125 0 01-1.667-.985V5.653z" />
            </svg>
            <span className=" border-primary/70 border-4 rounded-full w-20 h-20 absolute self-center right-1/2 translate-x-1/2 opacity-0 group-hover:opacity-100 group-hover:w-16 group-hover:h-16 duration-300"></span>
          </span>
          <span className="text-white my-2 truncate text-sm hover:text-primary duration-300 hover:cursor-pointer" onClick={() => navigateMe(movie.id, 'movie', movie.media_type)}>
            {movie.title || movie.name}
          </span>
          <div className="w-full relative text-xs flex text-whitePrimary/70">
            <Swiper className="" spaceBetween={0} slidesPerView={1} modules={[Autoplay, EffectFade]} autoplay={{ delay: 5000 }} fadeEffect={{ crossFade: true }} allowTouchMove={false} loop={true} effect="fade">
              {movie.genre_ids.map((id) => {
                const genreThis = [genreConverter(id, movie.media_type)];
                return <SwiperSlide key={id}>{({ isActive }) => <span className={`duration-400 flex ${isActive ? '-translate-y-0 opacity-100' : '-translate-y-full opacity-100'}`}>{genreThis}</span>}</SwiperSlide>;
              })}
            </Swiper>
            <span className=" truncate text-xs absolute content-center self-center right-2 z-1">{finalDate}</span>
          </div>
        </div>
      );
    }
    return null;
  });

  return (
    <div className="mx-24 mt-24">
      <div className="w-full h-96 rounded-3xl">
        <Swiper className="w-full h-96 rounded-3xl text-white font-quicksand" spaceBetween={0} slidesPerView={1} modules={[EffectFade, Autoplay]} autoplay={{ delay: 10000, disableOnInteraction: false }} effect="fade" loop={true}>
          <SwiperSlide className="w-full h-full relative">
            {({ isActive, isPrev, isNext }) => (
              <>
                <div className="absolute w-full h-full bg-black/40 inset-0"></div>
                <img src={Puss} className="w-full h-96 object-cover" alt="Dashboard" />
                <div className="absolute bottom-28 left-10 flex gap-1">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    strokeWidth={1.5}
                    stroke="currentColor"
                    className={`w-5 h-5 duration-300 ${isActive ? '-translate-x-0 opacity-100' : isPrev ? 'translate-x-1/2 opacity-0' : isNext ? '-translate-x-1/2 opacity-0' : ''}`}
                  >
                    <path strokeLinecap="round" strokeLinejoin="round" d="M12 6v6h4.5m4.5 0a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                  <span className={`duration-300 font-semibold ${isActive ? '-translate-x-0 opacity-100' : isPrev ? 'translate-x-1/2 opacity-0' : isNext ? '-translate-x-1/2 opacity-0' : ''}`}>1h 42m</span>
                </div>
                <div className={`absolute bottom-16 left-10 text-5xl font-semibold duration-300 ${isActive ? '-translate-y-0 opacity-100' : isPrev ? 'translate-y-full opacity-0' : isNext ? '-translate-y-full opacity-0' : ''}`}>
                  Puss In Boots: The Last Wish
                </div>
                <div className={`absolute bottom-9 left-11 text-whitePrimary/80 font-medium duration-300 flex`}>
                  <span className={` delay-75 duration-300 ${isActive ? '-translate-y-0 opacity-100' : isPrev ? 'translate-y-full opacity-0' : isNext ? '-translate-y-full opacity-0' : ''}`}>28/</span>
                  <span className={` delay-150 duration-300 ${isActive ? '-translate-y-0 opacity-100' : isPrev ? 'translate-y-full opacity-0' : isNext ? '-translate-y-full opacity-0' : ''}`}>12/</span>
                  <span className={` delay-200 duration-300 ${isActive ? '-translate-y-0 opacity-100' : isPrev ? 'translate-y-full opacity-0' : isNext ? '-translate-y-full opacity-0' : ''}`}>2022</span>
                </div>
              </>
            )}
          </SwiperSlide>
          <SwiperSlide className="w-full h-full">
            {({ isActive, isPrev, isNext }) => (
              <>
                <div className="absolute w-full h-full bg-black/40 inset-0"></div>
                <img src={SuperM} className="w-full h-96 object-cover" alt="Dashboard" />
                <div className={`absolute bottom-16 left-10 text-5xl font-semibold duration-300 ${isActive ? '-translate-y-0 opacity-100' : isPrev ? 'translate-y-full opacity-0' : isNext ? '-translate-y-full opacity-0' : ''}`}>
                  The Super Mario Bros. Movie
                </div>
                <div className={`absolute bottom-9 left-11 text-whitePrimary/80 font-medium duration-300 flex`}>
                  <span className={` delay-75 duration-300 ${isActive ? '-translate-y-0 opacity-100' : isPrev ? 'translate-y-full opacity-0' : isNext ? '-translate-y-full opacity-0' : ''}`}>07/</span>
                  <span className={` delay-150 duration-300 ${isActive ? '-translate-y-0 opacity-100' : isPrev ? 'translate-y-full opacity-0' : isNext ? '-translate-y-full opacity-0' : ''}`}>04/</span>
                  <span className={` delay-200 duration-300 ${isActive ? '-translate-y-0 opacity-100' : isPrev ? 'translate-y-full opacity-0' : isNext ? '-translate-y-full opacity-0' : ''}`}>2022</span>
                </div>
              </>
            )}
          </SwiperSlide>
          <SwiperSlide className="w-full h-full">
            {({ isActive, isPrev, isNext }) => (
              <>
                <div className="absolute w-full h-full bg-black/40 inset-0"></div>
                <img src={Santa} className="w-full h-96 object-cover" alt="Dashboard" />
                <div className="absolute bottom-28 left-10 flex gap-1">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    strokeWidth={1.5}
                    stroke="currentColor"
                    className={`w-5 h-5 duration-300 ${isActive ? '-translate-x-0 opacity-100' : isPrev ? 'translate-x-1/2 opacity-0' : isNext ? '-translate-x-1/2 opacity-0' : ''}`}
                  >
                    <path strokeLinecap="round" strokeLinejoin="round" d="M12 6v6h4.5m4.5 0a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                  <span className={`duration-300 font-semibold ${isActive ? '-translate-x-0 opacity-100' : isPrev ? 'translate-x-1/2 opacity-0' : isNext ? '-translate-x-1/2 opacity-0' : ''}`}>1h 52m</span>
                </div>
                <div className={`absolute bottom-16 left-10 text-5xl font-semibold duration-300 ${isActive ? '-translate-y-0 opacity-100' : isPrev ? 'translate-y-full opacity-0' : isNext ? '-translate-y-full opacity-0' : ''}`}>
                  Violent Night
                </div>
                <div className={`absolute bottom-9 left-11 text-whitePrimary/80 font-medium duration-300 flex`}>
                  <span className={` delay-75 duration-300 ${isActive ? '-translate-y-0 opacity-100' : isPrev ? 'translate-y-full opacity-0' : isNext ? '-translate-y-full opacity-0' : ''}`}>02/</span>
                  <span className={` delay-150 duration-300 ${isActive ? '-translate-y-0 opacity-100' : isPrev ? 'translate-y-full opacity-0' : isNext ? '-translate-y-full opacity-0' : ''}`}>12/</span>
                  <span className={` delay-200 duration-300 ${isActive ? '-translate-y-0 opacity-100' : isPrev ? 'translate-y-full opacity-0' : isNext ? '-translate-y-full opacity-0' : ''}`}>2022</span>
                </div>
              </>
            )}
          </SwiperSlide>
        </Swiper>
      </div>
      <div className="w-full flex justify-between items-center bg-secondary/60 text-white py-6 px-6 rounded-xl my-10">
        <div className="bg-primaryDark px-6 py-3 gap-6 flex rounded-3xl relative">
          <button className={`hover:text-primary duration-250 z-1 ${btnFilter === 1 ? 'text-primary' : 'text-white'}`} onClick={() => setBtnFilter(1)}>
            Featured
          </button>
          <button className={`hover:text-primary duration-250 z-1 ${btnFilter === 2 ? 'text-primary' : 'text-white'}`} onClick={() => setBtnFilter(2)}>
            Popular
          </button>
          <button className={`hover:text-primary duration-250 z-1 ${btnFilter === 3 ? 'text-primary' : 'text-white'}`} onClick={() => setBtnFilter(3)}>
            Newest
          </button>
          <div className=" bg-secondary/90 absolute top-1/2 -translate-y-1/2 rounded-xl duration-300" style={{ width: `${bt1_w}px`, height: `${btn_h}px`, left: '1rem' }} id="animationBg"></div>
        </div>
        <button className="flex justify-center gap-5 relative w-32 group rounded-sm" onClick={() => setBtnGenre((prev) => !prev)}>
          <span>{selectedGenre}</span>
          <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-5 h-5 self-center">
            <path strokeLinecap="round" strokeLinejoin="round" d="M19.5 8.25l-7.5 7.5-7.5-7.5" />
          </svg>
          {/* eslint-disable-next-line */}
          <div className={`w-full mt-8 absolute bg-primaryDark/95 text-white ${btnGenre == 1 ? '' : 'hidden'} z-1`} id="genres">
            <ul className="parent flex flex-col overflow-y-auto -scroll-m-10 scrollbar-hide min-h-0 max-h-[90vh]" id="parent">
              {getGenresList}
            </ul>
          </div>
        </button>
      </div>

      <div className=" flex flex-wrap justify-evenly min-h-screen relative">{!mainLoading ? popularMoviesList : <Lottie animationData={Loading} className="w-14 absolute self-center top-0" />}</div>
    </div>
  );
};
