import axios from 'axios';

const apiKey = process.env.REACT_APP_APIKEY;
const baseUrl = process.env.REACT_APP_BASEURL;

export const genreConverter = (id, type) => {
  if (type === 'tv') {
    switch (id) {
      case 10759:
        return 'Action & Adventure';
      case 16:
        return 'Animation';
      case 35:
        return 'Comedy';
      case 80:
        return 'Crime';
      case 99:
        return 'Documentary';
      case 18:
        return 'Drama';
      case 10751:
        return 'Family';
      case 10762:
        return 'Kids';
      case 9648:
        return 'Mystery';
      case 10763:
        return 'News';
      case 10764:
        return 'Reality';
      case 10765:
        return 'Sci-Fi & Fantasy';
      case 10766:
        return 'Soap';
      case 10767:
        return 'Talk';
      case 10768:
        return 'War & Politics';
      case 37:
        return 'Western';
      default:
        return 'Unknown';
    }
  } else {
    switch (id) {
      case 28:
        return 'Action';
      case 12:
        return 'Adventure';
      case 16:
        return 'Animation';
      case 35:
        return 'Comedy';
      case 80:
        return 'Crime';
      case 99:
        return 'Documentary';
      case 18:
        return 'Drama';
      case 10751:
        return 'Family';
      case 14:
        return 'Fantasy';
      case 36:
        return 'History';
      case 27:
        return 'Horror';
      case 10402:
        return 'Music';
      case 9648:
        return 'Mystery';
      case 10749:
        return 'Romance';
      case 878:
        return 'Science Fiction';
      case 10770:
        return 'TV Movie';
      case 53:
        return 'Thriller';
      case 10752:
        return 'War';
      case 37:
        return 'Western';
      default:
        return 'Unknown';
    }
  }
};

export const getMovieLatest = async () => {
  const movie = await axios.get(
    `${baseUrl}/discover/movie?api_key=${apiKey}&language=en-US&sort_by=primary_release_date.desc&include_adult=false&include_video=false&page=1&primary_release_date.lte=2023-01-26&with_watch_monetization_types=flatrate`
  );

  return movie.data.results;
};
export const getMoviePopular = async (sort, pages, genre) => {
  const genreResult = genre ? `&with_genres=${genre}` : '';
  const pageResult = pages ? `&page=${pages}` : `&page=1`;
  const sortResult = sort ? `&sort_by=${sort}` : `&sort_by=popularity.desc`;
  const movie = await axios.get(`${baseUrl}/discover/movie?api_key=${apiKey}${pageResult}${sortResult}${genreResult}`);
  console.log(`${baseUrl}/discover/movie?api_key=${apiKey}${pageResult}${sortResult}${genreResult}}`);
  return movie.data.results;
};
export const getMovieTrending = async () => {
  const movie = await axios.get(`${baseUrl}/trending/all/week?api_key=${apiKey}&page=1`);
  return movie.data.results;
};

export const getMovieDetail = async (movieId) => {
  const movie = await axios.get(`${baseUrl}/movie/${movieId}?api_key=${apiKey}`);
  return movie.data;
};

export const getTvDetail = async (tvId) => {
  const movie = await axios.get(`${baseUrl}/tv/${tvId}?api_key=${apiKey}`);
  return movie.data;
};

export const getGenres = async () => {
  const genres = await axios.get(`${baseUrl}/genre/movie/list?api_key=${apiKey}`);
  return genres.data.genres;
};

export const searchMovie = async (q) => {
  const search = await axios.get(`${baseUrl}/search/multi?api_key=${apiKey}&query=${q}&page=1`);
  console.log(search.data.results);
  return search.data;
};
